<template>
  <view class="uni-form-item uni-column container">
    <view class="title">
      <span class="title-content" v-text="title"></span>
      <span class="title-tips" v-text="titleTips"></span>
    </view>
    <sunui-upimg></sunui-upimg>
  </view>
</template>

<script>
import sunuiUpimg from '@/packageSubPage/components/sunui-upimg.vue';

export default {
  name: 'image-uploader',
  props: {
    title: {
      type: String,
      default: ''
    },
    titleTips: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: ''
    },
    dataType: {
      type: String,
      default: ''
    },
    dataKey: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      inputValue: ''
    };
  },
  components: {
    sunuiUpimg
  },
  watch: {
    inputValue(val) {
      this.$emit('onChangeValue', {
        recordType: this.dataType,
        contentType: this.dataKey,
        value: val
      });
    }
  }
};
</script>

