.container {
  background-color: #FFFFFF;
  margin-bottom: 14px;
  padding: 16px;
  border-radius: 8px;
}

.title {
  margin-bottom: 10px;
}

.title-content {
  color: #2E2E2E;
  font-size: 16px;
  line-height: 24px;
}

.title-tips {
  display: inline-block;
  margin-left: 10px;
  color: #C1C1C1;
  font-size: 14px;
  line-height: 20px;
}

</style>
